import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Languages, LanguageContext } from './LanguageContext.js';

class BioSoprano extends Component{

    render(){
        return(
            <>
            <LanguageContext.Consumer>
            {({siteLanguage, toggleLanguage}) => ( 
                <Container className="Main-Container">
                    <Row>
                        <Col className="header-color" style={{paddingLeft:"30px"}}>
                        {siteLanguage === Languages.en && <h3>Colloratura Soprano</h3>}
                        {siteLanguage === Languages.tr && <h3>Koloratur Soprano</h3>}
                        </Col>
                    </Row>
                    <Row>
                        <Col style={{paddingTop:"20px", paddingBottom:"30px", paddingLeft:"30px", paddingRight:"20px"}}>               
                        { 
                            siteLanguage === Languages.en &&
                            <div>
                                She was born in Mnich, Germany. She is the grand-daughter of Hafz Burhan Grses who was the singing teacher of the famous Turkish Classical Music singer, Hamiyet Yceses. He sang in the stanbul Radio House during the period of Mnir Nurettin Seluk. His singing teacher gave him the Grses (strong, flowing voice) surname during the Surname Law.
                                <br /><br />
                                Since primary school she had sung in choirs. In 1992, she was graduated from 9 September University, English Teaching as a Second Language, BA diploma. In 1993, she started singing in zmir French Cultural Association Classical Music Choir and also started taking singing lessons from Sabahat Tekeba. Between 1995-2001 she studied Performing Arts in Opera and Musical in stanbul Mimar Sinan State Conservatory, BM diploma. During her study she worked with the following singing coaches; Lynn Trepel alar, Azra Gn, Payam Koryak.
                                <br /><br />
                                Since 1995, she has been giving Classical Music, Christmas Carol and Broadway Musical Concerts in French, Austrian, Italian Cultural Centers, Bosphorus Univ., Robert College etc.
                                <br /><br />
                                Between 1996-2004, she worked for stanbul State Opera House as a contracted solo singer, her performances were:<br />
                                The lead role Nina in the Nutcracker children musical based on Tchaikovskys music.<br />
                                Conservatory graduation complete opera lead role Musetta from La Bohme Opera, Puccini and Faur Requiem, soprano solo with MS Orchestra, conducted by Fahrettin Kerimov.<br />
                                Do you like Mozart?, sang The Magic Flute Queen of the Night.<br />
                                Telephone comic opera, lead role Lucy.<br />
                                In 1999, she attended a Bel Canto singing workshop of Leyla Gencer in Atatrk Cultural Centre.
                                <br /><br />
                                She sang classical and musical repertoire in Turkuaz Music Group touring around and out of Turkey.
                                In 2003, she attended a vocal singing workshop of Eva Blahov Wiener Musik Seminar in Vienna and gave a recital in Bsendorfer Saal.
                                <br /><br />
                                In 2006, she attended the workshop of French percussionist Le Quan Ninh Transit-stanbul Improvisation Days in Music. She was invited by Islak Kpek  Group for vocal improvisation in their performance. She also did a vocal improvisation for the improvisational dance performance in Darphane-i Amire.
                                <br /><br />
                                Apart from being a singer, she worked as an English, music, drama, dance, singing and Turkish Culture teacher in international schools, including her conservatory MSU she was graduated from.
                                In 2003 starting as a hobby, after studying and making research about alternative healing, she studied the healing power of the human voice Sound Healing technique with Jonathan Goldman in Boulder, Denver, Colorado and received a sound healer certificate. She is the first and only Turkish Sound Healer in Turkey who is also the member of World Sound Healers Association. She continues her sound healing career by giving private and group sessions, workshops and doing vocal improvisation.
                                <br /><br />
                                In 2008, she gave a Christmas Carol Concert in Kurueme Divan, for the Dutch Consulate. For the opening of 2009 Istanbul Biennial, she gave a Classical Concert in the Dutch Consulate. In Sept. 2014, she sang in , 3 Sopranos Classic Summer Recital in the Dutch Chapel, stanbul.
                                <br /><br />
                                After a long intermission in her singing career; she got married to the Dutch chef, photographer, documentary program maker Wilco van Herpen and has a daughter whom she has brought up to the age of 6, it is high time to get back to what she loves most, singing                        
                            </div>
                        }        
                        { 
                            siteLanguage === Languages.tr &&
                            <div>
                                Hamiyet Ycesesin ilk an hocas olan ve ayn zamanda stanbul Radyo Evinde Mnir Nurettin Selukla ayn dnemde Trk musikisi icra etmi, soyad kanunu ktnda, an hocas tarafndan Grses soyad verilen, Hafz Burhan Grsesin torunu, Gonca Grses van Herpen Mnih, Almanyada dodu.
                                <br /><br />
                                Mzie ilgisi ilkokul yllarnda koroya katlmasyla balad. 1992de 9 Eyll niversitesi ngilizce retmenlii blmnden mezun olduktan sonra zmir Fransz Kltr Derneinin klasik mzik korosuna katld ve yine ayn dnemde hayalini gerekletirmek istedii mzik yolculuuna, Sabahat Tekebadan an dersleri alarak balam oldu.
                                <br /><br />
                                1995-2001 yllarnda Mimar Sinan niversitesi Konservatuar Sahne Sanatlar Opera Ana Sanat Dalnda opera ve mzikal eitimi ald. rencilii boyunca Lynn Trepel alar, Azra Gn ve Payam Koryak gibi deerli isimlerle alt.
                                Fransz Kltr Merkezi, Avusturya Kltr Merkezi, talyan Kltr Merkezi, Boazii niversitesi, Robert Kolej de dahil olmak zere bir ok yerde klasik, Broadway mzikalleri, yeni yl arklarndan oluan konserler verdi.
                                Okul mezuniyet
                                <br /><br />
                                stanbul Devlet Operasnda szlemeli olarak alt dnemde(1996-2004): Tchaikovskynin Fndkkran ocuk mzikalinde barol (Nina), okul mezuniyeti kapsamnda Fahrettin Kerimov ynetiminde MS orkestras eliinde La Bohme Operas Musetta rol ve Faur Requiem soprano solo syledi. Mozart Sever Misiniz? eserinde Sihirli Flt Operasndan Gece Kraliesini syledi. Menotti Telefon operasnda barol ( Lucy) oynad.
                                1999 ylnda Atatrk Kltr Merkezinde gerekletirilen Bel Canto Solist Atlyesinde Leyla Gencerle alt.
                                Turkuaz mzik grubunda klasik ve mzikal repertuar ile yurtii ve yurtdnda bir ok yerde sahneye kt.
                                <br /><br />
                                2003 ylnda Viyanada Eva Blahov (Wiener Musik Seminar) solist atlyesine katld ve Bsendorfer Saalda resital verdi.
                                <br /><br />
                                2006 ylnda Fransz perksyoncu Le Quan Ninhin Transit-stanbul Improvisation Days in Music \ Mzikte Doalama atlyesine katld. Islak Kpek Grubunun konserinde konuk solist olarak doalama performans gerekletirdi. Ayrca Darphane-i Amirede doalama dans gsterisinde yine solo doalama syledi.
                                <br /><br />
                                Opera solistliinin yan sra MS ve uluslararas birok okulda ngilizce dersi, ngilizce olarak drama, mzik, dans, drama, an, Trk kltr alanlarnda dersler verdi.
                                <br /><br />
                                2003 ylnda hobi olarak balad alternatif ifa teknikleri aratrmalar ve eitimlerinin ardndan Colorado, Denver, Boulderda Jonathan Goldmanla  Sound Healing / Sesle ifa  atlyesine katld. Buradan ald sertifikayla Trkiyede bu alanda almalar yapan ayn zamanda Dnya Sesle ifa Enstitsne kaytl ilk ve tek Trk Sesle ifa Terapisti oldu. Halen bireysel seanslar, grup almalar, seminerler vererek ve vokal doalamalar yaparak bu konudaki almalarna devam etmektedir.
                                <br /><br />
                                2008 ylnda Hollanda Konsolosluu etkinlikleri kapsamnda, Kurueme Divanda ylba konseri verdi. 2009 stanbul Bienal al iin Hollanda Konsolosluunda klasik mzik konseri verdi. 2014 ylnda Hollanda Konsolosluu Kilisesinde iki soprano ile Klasik Yaz Konseri verdi.
                                <br /><br />
                                Sanat, Hollandal a, fotoraf, belgesel programcs Wilco van Herpen ile evli ve bir kz ocuu annesi.
                                <br /><br />
                                Uzun ve ok ynl srdrd mzik kariyerine, kznn doumuyla verdii arann ardndan yeniden dnd. Mzie duyduu akla ve ok ynl almalaryla tecrbe katt sesini, imdilerde ok daha geni kitlelere duyurmak niyetiyle konserlerini ve almalarn srdryor.             
                            </div>
                        }     
                        </Col>
                    </Row>
                </Container>
                )}
                </LanguageContext.Consumer>
            </>
        );
    }
}

export default BioSoprano;